@import "../sass/mixins/_mixins";

.section-team {
  padding: 10rem 0 11.2rem;
  background: url(../assets/images/bg/bg-team.png) no-repeat 50% 110%;
  @include lg {
    padding: 0 0 10rem;
    // background-position: ;
    // background-size: contain;
    background-size: 800px;
  }
  .title {
    margin-bottom: 4.8rem;
    @include lg {
      margin-bottom: 1.6rem;
    }
  }
  .desc {
    &:not(:last-child) {
      margin-bottom: 2.4rem;
    }
  }
}

.teams {
  margin-top: 6rem;
  @include lg {
    margin-top: 2.4rem;
  }
  .swiper-slide {
    @include lg {
      width: 276px;
    }
  }
}

.team {
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(8px);
  border-radius: 32px;
  display: flex;
  @include transition-slow;
  &:hover {
    background: rgba(255, 255, 255, 0.06);
  }
  &-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3.2rem;
    justify-content: flex-start;
    width: 100%;
    &__img {
      margin-bottom: 3.2rem;
      img {
        border-radius: 50%;
      }
    }
    &__title {
      font-weight: 700;
      font-size: 1.6rem;
      line-height: 2rem;
      color: #FFFFFF;
      text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.35);
      margin-bottom: 1.4rem;
    }
    &__desc {
      margin-bottom: 1.4rem;
      background: rgba(255, 255, 255, 0.24);
      backdrop-filter: blur(20px);
      border-radius: 12px;
      display: inline-flex;
      padding: .4rem 1.2rem;
      font-weight: 600;
      font-size: 1.4rem;
      line-height: 2.8rem;
      text-align: center;
      color: #FFFFFF;
    }
  }
}
