/*CORE STYLES*/
@import "../mixins/mixins.sass";

html {
  font-size: 62.5%; }

body {
  background: #1F004A;
  background-size: cover;
  position: relative;
  font-variant: normal;
  font-feature-settings: normal;
  margin: 0;
  @include font(1.6rem, 1.8em, 400);
  transition: background-color .1s ease-out;
  overflow-x: hidden;
  color: rgba(#B6A0D5, .8);
  font-family: 'Poppins'; }

//transition show images
img.lazy {
  opacity: 0;
  @include transition;
  &[lazy=loaded] {
    opacity: 1; } }
