@import "../sass/mixins/_mixins";

.section-roadmap {
  padding: 2rem 0 6.2rem;
  @include lg {
    padding: 0 0 10.4rem;
    // display: none;
  }
  .title {
    margin-bottom: 6rem;
    text-align: center;
  }
}

.roadmaps-box {
  position: relative;
  margin-top: 6rem;
  .roadmap-center {
    width: 1.2rem;
    position: absolute;
    top: 0;
    bottom: 0;
    left: calc(50% - .6rem);
    background: rgba(255, 255, 255, 0.12);
    border-radius: 30px;
    @include lg {
      left: 0;
    }
    & > span {
      top: .2rem;
      left: .2rem;
      right: .2rem;
      background: #E21E85;
      box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.45), 0px 0px 17px #E21E85;
      border-radius: 30px;
      position: absolute;
      z-index: 10;
    }
  }
}

.roadmaps {
  .roadmap-line {
    &:not(:first-child) {
      margin-top: -4rem;
      @include lg {
        margin-top: 1.2rem;
      }
    }
    // &:not(:last-child)
  }
}

.roadmap {
  width: calc(50% - 48px);
  box-shadow: 0px 12px 29px rgba(0, 0, 0, 0.16);
  border-radius: 32px;
  padding: 3.2rem;
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(24px);
  @include transition-slow;
  @include lg {
    width: calc(100% - 28px);
    padding: 2.4rem;
  }
  &.active {
    box-shadow: 0px 12px 29px rgba(0, 0, 0, 0.16);
    background: rgba(255, 255, 255, 0.06);
    backdrop-filter: blur(24px);
    .roadmap-item{
      &__desc {
        color: #FFFFFF;
        ul {
          li {
            &:before {
              background: #ffffff;
            }
          }
        }
      }
    }
  }
  &-line {
    display: flex;
    width: 100%;
    &__left {
      justify-content: flex-start;
      @include lg {
        justify-content: flex-end;
        // margin-top: -8rem;
      }
    }
    &__right {
      justify-content: flex-end;
      @include lg {
        justify-content: flex-end;
      }
    }
  }
  &-item {
    &__header {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 2.4rem;
      @include lg {
        margin-bottom: 1.6rem;
      }
      &-date {
        font-weight: 700;
        font-size: 1.6rem;
        line-height: 2.4rem;
        text-transform: uppercase;
        color: #FFFFFF;
        padding: .8rem 1.2rem;
        background: rgba(255, 255, 255, 0.24);
        backdrop-filter: blur(20px);
        border-radius: 12px;
        @include lg {
          text-transform: none;
          font-size: 1.4rem;
          line-height: 2rem;
          white-space: nowrap;
        }
        &.color-pink {
          background: #E21E85;
        }
        &.color-blue {
          background: #00CFFF;
        }
      }
      &-status {
        font-weight: 600;
        font-size: 1.4rem;
        line-height: 2rem;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.32);
        @include lg {
          flex-grow: 1;
          text-align: right;
        }
        &.color-pink {
          color: #E21E85;
        }
        &.color-blue {
          color: #00CFFF;
        }
      }
    }
    &__desc {
      font-weight: 300;
      font-size: 1.4rem;
      line-height: 2rem;
      color: rgba(255, 255, 255, 0.6);
      font-family: 'Poppins', sans-serif;
      ul {
        display: block;
        margin: 0;
        padding: 0;
        li {
          display: block;
          position: relative;
          padding: 0 0 0 2rem;
          &:before {
            content: " ";
            display: block;
            position: absolute;
            z-index: 1;
            background: rgba(255, 255, 255, 0.4);
            width: .8rem;
            height: .8rem;
            border-radius: 50%;
            top: .6rem;
            left: 0;
          }
          &:not(:last-child) {
            margin-bottom: 1.2rem;
          }
        }
      }
    }
  }
}
