@import "../sass/mixins/_mixins";

.v-enter-active,
.v-leave-active {
  transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
  opacity: 0;
}

.logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.open-menu {
  display: inline-flex;
  width: 2.4rem;
  height: 2.4rem;
  align-items: center;
  justify-content: center;
  margin-right: 1.2rem;
  cursor: pointer;
}

.nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 4rem;
  width: 100%;
  a {
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 2rem;
    text-transform: uppercase;
    @include transition;
    color: rgba(#FFFFFF, .4);
    &:hover, &.active {
      color: rgba(#FFFFFF, 1);
    }
    &:not(:last-child) {
      margin-right: 1.6rem;
    }
    &:last-child {
      margin-left: auto;
    }
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  padding-top: 6.4rem;
  @include lg {
    padding-top: 1.2rem;
  }
  &-desctop {
    position: relative;
    z-index: 10;
  }
  &-mobile {
    position: absolute;
    z-index: 5;
    width: 100%;
    height: 100vh;
    background: rgba(255, 255, 255, 0.02);
    backdrop-filter: blur(128px);
    top: 0;
    left: 0;
    right: 0;
    nav {
      flex-direction: column;
      align-items: center;
      height: 100vh;
      justify-content: center;
      a {
        &:not(:last-child) {
          margin: 0 0 3.2rem;
        }
        &:last-child {
          position: absolute;
          bottom: 4rem;
        }
      }
    }
  }
  &-offset {
    &-1 {
      margin-left: auto;
      @include lg {
        margin-left: 0;
      }
    }
    &-2 {
      margin-left: 4rem;
      @include lg {
        margin-left: 0;
      }
    }
  }
}

.lang-switcher {
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    margin-left: 1.2rem;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 2rem;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.4;
    @include transition-slow;
    &:hover,
    &.current {
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 2rem;
      color: #FFFFFF;
      opacity: 1;
    }
  }
}
