.title {
  @include font(4.8rem, 1em, bold);
  margin-bottom: 3.2rem;
  color: $white;
  text-transform: uppercase;
  font-family: 'Poppins', sans-serif;
  font-weight: 900;
  font-size: 6rem;
  line-height: 6.8rem;
  color: #FFFFFF;
  text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.35);
  @include lg {
    font-weight: 900;
    font-size: 4rem;
    line-height: 4.8rem; }
  span {
    color: $secondary; }
  // +lg
  //   @include font(3rem, 1.2em, bold)
 }  //   margin-bottom: 2.4rem

.desc {
  font-family: 'Poppins';
  @include lg {
    @include font(1.4rem, 2.2rem, 400); } }

.btn-round {
  @include font(1.4rem, 1.71em, 600);
  color: $white;
  border-radius: 50%;
  border-bottom: 6px solid $primary-t;
  border-left: 2px solid $primary-t;
  border-right: 2px solid $primary-t;
  border-top: 2px solid $primary-t;
  background-color: $primary;
  width: 9.6rem;
  height: 9.6rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  @include transition;
  &:hover {
    background-color: rgba($primary, 0.8); }
  &:active {
    background-color: $primary; } }

.btn, a.btn, button.btn {
  display: inline-flex;
  @include font(1.4rem, 2rem, 700);
  font-family: 'Poppins';
  align-content: center;
  justify-content: center;
  color: $white;
  &-base {
    background: #00CFFF;
    box-shadow: 0px 2px 0px #0696B8, 0px 4px 6px rgba(0, 0, 0, 0.11), 0px 20px 64px rgba(0, 207, 255, 0.36);
    border-radius: 1.2rem;
    @include transition-slow;
    color: $white;
    &:hover, &:active {
      background: #FFFFFF;
      color: #00CFFF;
      box-shadow: 0px 2px 0px #0696B8, 0px 4px 6px rgba(0, 0, 0, 0.11), 0px 20px 64px rgba(0, 207, 255, 0.36); }

    &[disabled],
    &.disabled {
      background: #00CFFF;
      opacity: 0.4;
      box-shadow: 0px 2px 0px #0696B8, 0px 4px 6px rgba(0, 0, 0, 0.11), 0px 20px 64px rgba(0, 207, 255, 0.36);
      border-radius: 12px; } }

  &-pink {
    background: #E21E85;
    box-shadow: 0px 2px 0px #9C155C, 0px 4px 6px rgba(0, 0, 0, 0.11), 0px 20px 64px rgba(226, 30, 133, 0.36);
    border-radius: 12px;
    border-radius: 1.2rem;
    @include transition-slow;
    color: $white;
    &:hover, &:active {
      color: #E21E85;
      background: #FFFFFF;
      box-shadow: 0px 2px 0px #9C155C, 0px 4px 6px rgba(0, 0, 0, 0.11), 0px 20px 64px rgba(226, 30, 133, 0.36); }
    &[disabled],
    &.disabled {
      background: #E21E85;
      opacity: 0.4;
      box-shadow: 0px 2px 0px #9C155C, 0px 4px 6px rgba(0, 0, 0, 0.11), 0px 20px 64px rgba(226, 30, 133, 0.36);
      border-radius: 12px; } }

  &-lg {
    padding: 2rem 5.2rem;
    text-transform: uppercase; } }

.link, a.link, button.link {
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 2rem;
  text-transform: uppercase;
  color: rgba(#00CFFF, 1);
  @include transition-slow;
  &:hover {
    color: #FFFFFF; }
  &[disabled],
  &.disabled {
    color: #00CFFF;
    opacity: 0.4; } }

.soc {
  display: flex;
  justify-content: center;
  a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @include transition-slow;
    &:hover {
      transform: scale(1.1); }
    &:not(:last-child) {
      margin-right: .8rem; } } }

.container {
  max-width: 1200px;
  @include lg {
    width: 380px;
    max-width: 380px;
    margin: 0 auto;
    padding: 0 1.6rem; } }


