@import "../sass/mixins/_mixins";

.footer {
  padding: 3rem 0 4.6rem;
  background: #150132;
  &-line {
    border-top: 1px solid rgba(#FFFFFF, .08);
    margin-top: 5.8rem;
    margin-bottom: 1.8rem;
    @include lg {
      margin: 2.4rem 0;
    }
  }
  &-logo {
    margin-bottom: 3.4rem;
    @include lg {
      margin-bottom: 1.2rem;
    }
  }
  &-copy {
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 1.6rem;
    letter-spacing: -0.04em;
    color: #FFFFFF;
    opacity: 0.4;
    font-family: 'Poppins';
    @include lg {
      margin-top: 1.6rem;
    }
  }
  .author {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 2.4rem;
    letter-spacing: -0.04em;
    color: rgba(253, 253, 253, 0.32);
    font-family: 'Poppins';
    @include lg {
      margin-top: 1.6rem;
    }
    a {
      margin-left: .4rem;
      @include transition-slow;
      color: rgba(253, 253, 253, 1);
      &:hover {
        color: rgba(253, 253, 253, 0.32);
      }
    }
    svg {
      margin-left: .8rem;
    }
  }
  .fmenu {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    @include lg {
      flex-wrap: wrap;
      margin-bottom: .8rem;
    }
    a {
      font-family: 'Poppins';
      font-weight: 500;
      font-size: 1.4rem;
      line-height: 2rem;
      letter-spacing: -0.04em;
      color: #FFFFFF;
      opacity: 0.6;
      @include transition-slow;
      @include lg {
        flex-basis: calc(100% / 3);
      }
      &:hover {
        opacity: 1;
      }
      &:not(:last-child){
        margin-right: 3.6rem;
        @include lg {
          margin-right: 0;
        }
      }
    }
  }
  &-menu {
    &__title {
      font-weight: 600;
      font-size: 1.6rem;
      line-height: 2rem;
      letter-spacing: -0.04em;
      color: #FFFFFF;
      opacity: 0.2;
      margin-bottom: 1.2rem;
      font-family: 'Poppins';
    }
    ul {
      li {
        a {
          font-weight: 500;
          font-size: 1.4rem;
          line-height: 2rem;
          letter-spacing: -0.04em;
          color: #FFFFFF;
          opacity: 0.6;
          font-family: 'Poppins';
          @include transition-slow;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }
  &-soc {
    @include lg {
      margin-bottom: 2.4rem;
    }
    &__title {
      margin-bottom: 1.2rem;
      font-weight: 600;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.04em;
      color: #FFFFFF;
      opacity: 0.2;
    }
    &__soc {
      justify-content: flex-start;
      a {
        &:not(:last-child) {
          margin-right: 1.2rem;
        }
      }
    }
  }
}
