@import "../sass/mixins/_mixins";

.main {
  display: block;
  position: relative;
  overflow: hidden;
}

.section-top {
  display: block;
  min-height: 103rem;
  background: url(../assets/images/bg/bg.png) no-repeat 50% 0;
  @include lg {
    min-height: 66.5rem;
    background-size: 110rem;
    background-position: 50% 100%;
  }
  .front-img{
    text-align: center;
    @include lg {
      padding-top: 6rem;
      margin: 0 -6rem;
    }
    img {
      width: 700px;
      height: auto;
      @include lg {
        width: 100%;
      }
    }
  }
  h1 {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 14rem;
    line-height: 12.6rem;
    text-align: center;
    letter-spacing: -5px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.35);
    position: relative;
    margin-top: -30rem;
    margin-bottom: 2rem;
    @include lg {
      font-weight: 900;
      font-size: 6rem;
      line-height: 5.6rem;
      letter-spacing: -2px;
      margin-bottom: 1.6rem;
      margin-top: -18rem;
    }
  }
  .desc {
    text-align: center;
    font-family: 'Poppins';
    font-weight: 500;
    font-size: 2rem;
    line-height: 2.4rem;
    letter-spacing: 2px;
    color: #FFFFFF;
    text-shadow: 0px 1px 14px rgba(0, 0, 0, 0.69);
    margin-bottom: 4.2rem;
    @include lg {
      margin-bottom: 2.4rem;
      font-weight: 500;
      font-size: 1.6rem;
      line-height: 2.4rem;
      text-align: center;
      letter-spacing: 2px;
    }
  }
  .front-btns {
    display: flex;
    justify-content: center;
    .btn {
      // color: $white;
    }
  }
}
