@import "../sass/mixins/_mixins";

.section-projects {
  padding: 10rem 0;
  @include lg {
    padding: 10rem 0;
  }
  .title {
    margin-bottom: 2rem;
    text-align: center;
    @include lg {
      margin-bottom: 1.6rem;
    }
  }
  .desc {
    text-align: center;
    margin-bottom: 6rem;
    @include lg {
      margin-bottom: 2.4rem;
    }
  }
  .btn {
    @include lg {
      width: 100%;
    }
  }
}

.projects {
  margin-bottom: 4rem;
  @include lg {
    margin-bottom: 2rem;
  }
}

.project {
  display: flex;
  width: 100%;
  height: 100%;
  box-shadow: 0px 12px 29px rgba(0, 0, 0, 0.16);
  border-radius: 3.2rem;
  @include lg {
    height: auto;
    margin-bottom: 1.2rem;
  }
  &-item {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 19.2rem 3.2rem 3.2rem;
    @include lg {
      height: 35.2rem;
      padding: 2.4rem;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      border-radius: 3.2rem;
      z-index: -1;
      width: 100%;
      min-width: 270px;
      height: auto;
    }
    &__marker {
      background: rgba(255, 255, 255, 0.24);
      backdrop-filter: blur(20px);
      border-radius: 12px;
      position: absolute;
      top: 3.2rem;
      left: 3.2rem;
      display: inline-flex;
      padding: .4rem 1.2rem;
      color: #FFFFFF;
      font-weight: 600;
      font-size: 1.4rem;
      line-height: 2.8rem;
      @include lg {
        top: 2.4rem;
        left: 2.4rem;
      }
    }
    &__title {
      font-weight: 700;
      font-size: 1.6rem;
      line-height: 2rem;
      margin: 0 0 1.2rem;
      color: #FFFFFF;
      @include lg {
        margin-top: auto;
      }
    }
    &__desc {
      font-weight: 300;
      font-size: 1.4rem;
      line-height: 2rem;
      color: rgba(255, 255, 255, 0.6);
      @include lg {
        padding-right: 5rem;
      }
    }
  }
}
