@import "../sass/mixins/_mixins";

.section-about {
  padding: 10rem 0 5.2rem;
  background: url(../assets/images/bg/bg-about.png) no-repeat 50% 0;
  @include lg {
    background: none;
    padding-bottom: 0;
  }
  &__mobile {
    &-img {
      display: none;
      @include lg {
        display: block;
        height: 29.8rem;
        // width: 100%;
        margin: 0 -1.5rem;
        background: url(../assets/images/bg/bg-about.png) no-repeat calc(50% - 110px) 0;
        background-size: 545px;
      }
    }
  }
  .title {
    margin-bottom: 4.8rem;
    @include lg {
      margin-bottom: 1.6rem;
    }
  }
  .desc {
    &:not(:last-child) {
      margin-bottom: 2.4rem;
    }
  }
}

.about {
  box-shadow: 0px 12px 29px rgba(0, 0, 0, 0.16);
  border-radius: 32px;
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(24px);
  display: flex;
  justify-content: flex-start;
  // align-items: center;
  margin-top: 8.6rem;
  @include lg {
    margin-top: 0rem;
  }
  &__img {
    flex-basis: 33.6rem;
    min-width: 33.6rem;
    background: url(../assets/images/bg/bg-about2.png) no-repeat 0 50% / cover;
    // height: 100%;
    display: flex;
    border-radius: 32px 0 0 32px;
    +lg {
      border-radius: 32px;
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-size: cover;
    }
  }
  &__content {
    padding: 6.4rem 0 6.4rem;
    @include lg {
      padding: 2.4rem 1.6rem;
      position: relative;
      z-index: 10;
      background: rgba(255, 255, 255, 0.02);
      border-radius: 3.2rem;
      backdrop-filter: blur(24px);
    }
  }
  &__title {
    font-weight: 900;
    font-size: 2.8rem;
    line-height: 3.6rem;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 0px 4px 8px rgba(0, 0, 0, 0.35);
    margin-bottom: 2rem;
    @include lg {
      font-weight: 900;
      font-size: 4rem;
      line-height: 4.8rem;
      margin-bottom: 1.6rem;
    }
  }
  &__desc {
    line-height: 2.8rem;
  }
}
