@import "../sass/mixins/_mixins";

.section-join{
  padding: 10rem 0 0;
  min-height: 57.2rem;
  background: url(../assets/images/bg/bg-join.png) no-repeat 50% 0%;
  @include lg {
    background-size: 560px;
    background-position: 50% 50%;
    padding: 0 0 8rem;
    min-height: 0;
  }
  .title {
    margin-bottom: 8.2rem;
    text-align: center;
    @include lg {
      margin-bottom: 3.2rem;
    }
    span {
      color: #E21E85;
    }
  }
  .btns {
    display: flex;
    align-items: center;
    justify-content: center;
    @include lg {
      flex-direction: column;
    }
    .btn {
      text-transform: uppercase;
      width: 26rem;
      padding-left: 0;
      padding-right: 0;
      &:not(:last-child) {
        margin-right: 2rem;
        @include lg {
          margin-bottom: 1.6rem;
          margin-right: 0rem;
        }
      }
    }
  }
}
